<template>
    <div class="d-flex justify-center align-center" id="cola-start"
        :style="{'background-color': getCurentProduct.color}">
        <v-container fluid>
            <v-row class="d-flex justify-center align-center">

                <v-col class="d-flex d-md-none justify-center align-center" cols="12">
                    <div class="cola-card" v-for="(data, i) in getDataProducts" :key="i"
                        @click="changeProduct(data)">
                        <v-img
                            :src="require('../../assets/cola/'+data.src)"
                            class="my-3"
                            contain
                            height="80"/>
                    </div>
                </v-col>
                <v-col class="d-flex flex-column justify-center align-center" cols="12" md="5">
                    <div class="cola-hero-title">
                        <p class="cola-title-1"
                            :style="{'color': getCurentProduct.fontColor}">
                            that's what
                        </p>
                        <p class="cola-title-2"
                            :style="{'color': getCurentProduct.fontColor}">
                            <transition name="custom" mode="out-in"
                                enter-active-class="animate__animated animate__lightSpeedInLeft"
                                leave-active-class="animate__animated animate__lightSpeedOutRight">
                                <p :key="getCurentProduct.name">{{ getCurentProduct.name }}</p>
                            </transition>
                        </p>
                        <p class="cola-descript"
                            :style="{'color': getCurentProduct.fontColor}">
                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Sed, reprehenderit. Optio sit debitis veniam repellendus, natus earum sequi. Lorem ipsum dolor sit amet!
                        </p>
                    </div>
                </v-col>

                <v-col class="d-none d-md-flex justify-end" cols="12" md="5">
                    <div class="mx-5">
                        <transition name="custom" mode="out-in"
                            enter-active-class="animate__bounceIn"
                            leave-active-class="animate__bounceOut">
                            <v-img
                                :key="getCurentProduct.name"
                                :src="require('../../assets/cola/'+getCurentProduct.src)"
                                class="my-3 mx-5"
                                contain
                                height="500"
                                width="300"/>
                        </transition>
                    </div>
                    <div class="d-flex flex-column  mx-5"
                        style="width: 80px;">
                        <div class="cola-card" v-for="(data, i) in getDataProducts" :key="i"
                            @click="changeProduct(data)">
                            <v-img
                                :src="require('../../assets/cola/'+data.src)"
                                class="my-3"
                                contain
                                height="80"/>
                        </div>
                    </div>
                </v-col>

            </v-row>
        </v-container>
    </div>
</template>


<style scoped>
    #cola-start {
        min-height: 100vh;
        /*background-color: #0161b8;*/
        transition: .5s;
    }
    .cola-hero-title {

    }
    .cola-title-1, .cola-title-2 {
        font-family: 'Montserrat', sans-serif;
        /*color: white;*/
        text-transform: uppercase;
        transition: .5s;
    }
    .cola-title-1 {
        font-size: 5em;
        font-weight: 700;
        letter-spacing: 1px;
        line-height: .7em;
    }
    .cola-title-2 {
        font-size: 12em;
        font-weight: 900;
        letter-spacing: 1px;
        line-height: .8em;
    }
    .cola-descript {
        font-family: 'Montserrat', sans-serif;
        font-size: 1em;
        font-weight: 600;
        text-align: justify;
        transition: .5s;
    }
    .cola-card {
        cursor: pointer;
    }
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
    @media only screen and (max-width: 960px) {
        .cola-title-1, .cola-title-2 {
            text-align: center;
        }
        .cola-title-1 {
            font-size: 1.5em;
            line-height: 1em;
        }
        .cola-title-2 {
            font-size: 3em;
            line-height: 1em;
        }
        .cola-descript {
            text-align: center;
            padding: 20px;
        }
        .cola-card {
            width: 80px;
        }
    }
</style>

<script>
    import { mapGetters, mapActions } from 'vuex'
    export default {
        name: 'SectionHero',

        created() {
            let dataFirst = {
                name : 'pepsi',
                src : 'pepsi001.png',
                color : '#0161b8',
                fontColor : 'white',
            }
            this.changeProduct(dataFirst)
        },

        mounted: function() {
            let elHtml = document.getElementsByTagName('html')[0]
            elHtml.style.overflowY = 'hidden'
        },

        destroyed: function() {
            let elHtml = document.getElementsByTagName('html')[0]
            elHtml.style.overflowY = null
        },

        computed: {
            ...mapGetters({
                getCurentProduct: 'getCurentProduct',
                getDataProducts: 'getDataProducts',
            }),
        }, 

        data: () => ({
            // 
        }),

        methods: {
            ...mapActions({
                setCurentProduct: 'setCurentProduct',
            }),
            changeProduct(data) {
                this.setCurentProduct(data)
            },
        },
    }
</script>