<template>
    <div id="onepage-four" class="d-flex justify-center align-center">
        <v-container fluid class="">
            <v-row class="d-flex justify-center align-center">
                <v-col cols="12" md="8" lg="5" class="pagefour-box">
                    <div class="pagefour-box-content d-flex justify-md-start justify-center align-center">

                        <div class="pagefour-box-img d-flex justify-center align-center">
                            <transition name="custom" mode="out-in"
                                enter-active-class="animate__animated animate__fadeIn"
                                leave-active-class="animate__animated animate__fadeOut">
                                <v-img
                                    :key="curentProduct.id"
                                    :src="require('../../assets/onepage/nike-'+curentProduct.name+'.png')"
                                    style=""
                                    class="pagefour-img my-3"
                                    contain
                                    height="500"/>
                            </transition>
                        </div>
                    </div>
                </v-col>
                <v-col cols="12" md="8" lg="5" class="pagefour-box">
                    <div v-for="(item, i) in dataProduct" :key="i" class="pagefour-box-card d-flex flex-column justify-center align-start my-4"
                        v-tilt="{speed: 500, perspective: 1200}"
                        :class="curentProduct.id == item.id ? 'pagefour-box-card-active' : ''"
                        @click="changeCurentProduct(i)">
                        <div class="pagefour-card-title-1"
                            :class="curentProduct.id == item.id ? 'pagefour-box-card-active' : ''">
                            nike
                        </div>
                        <div class="pagefour-card-title-2"
                            :class="curentProduct.id == item.id ? 'pagefour-box-card-active' : ''">
                            {{ item.name }}
                        </div>
                    </div>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<style scoped>
    #onepage-four {
        min-height: 100vh;
        background-color: #171717;
    }
    .pagefour-box {
        /**/
    }
    .pagefour-box-content {
        /**/
    }
    .pagefour-box-img {
        width: 90%;
        position: relative;
        /*background-color: #272727;*/
        border-radius: 20px;
        border: 4px solid crimson;
        transform: rotate(-20deg);
    }
    .pagefour-box-img:hover {
        cursor: pointer;
        box-shadow: 0 0 10px crimson,
                    0 0 20px crimson,
                    inset 0 0 10px crimson,
                    inset 0 0 20px crimson;
    }
    .pagefour-box-img::before, .pagefour-box-img::after {
        transition: .5s;
        font-size: 7em;
        transform: rotate(-18deg);
    }
    .pagefour-box-img::before {
        content: 'NIKE';
        position: absolute;
        top: 0;
        left: 2%;
        font-family: 'Montserrat', sans-serif;
        font-weight: 900;
        color: white;
        font-style: italic;
        opacity: 1;
    }
    .pagefour-box-img::after {
        content: 'STYLE';
        position: absolute;
        bottom: -50px;
        right: 5%;
        font-family: 'Montserrat', sans-serif;
        font-weight: 900;
        color: white;
        font-style: italic;
        opacity: 1;
    }
    .pagefour-img {
        transform: rotate(20deg);
        border:4px solid white;
        border-radius: 20px;
        z-index: 1;
    }
    .pagefour-box-card {
        background-color: #272727;
        height: 12em;
        border-radius: 20px;
        box-sizing: border-box;
        position: relative;
        /*transition: .5s;*/

        transform-style: preserve-3d;
        transform: perspective(1000px);
    }
    .pagefour-box-card::before, .pagefour-box-card::after {
        transition: .5s;
        font-size: 4em;
        /*transform: rotate(-18deg);*/
    }
    .pagefour-box-card::before {
        content: 'EXTRA';
        position: absolute;
        top: 0;
        left: 10px;
        font-family: 'Montserrat', sans-serif;
        font-weight: 900;
        color: white;
        font-style: italic;
        opacity: .05;
    }
    .pagefour-box-card::after {
        content: 'ORDINARY';
        position: absolute;
        bottom: 0;
        right: 1em;
        font-family: 'Montserrat', sans-serif;
        font-weight: 900;
        color: white;
        font-style: italic;
        opacity: .05;
    }
    .pagefour-box-card:hover {
        border: 4px solid crimson;
        cursor: pointer;
    }
    .pagefour-card-title-1, .pagefour-card-title-2 {
        font-family: 'Montserrat', sans-serif;
        font-size: 4.5em;
        font-weight: 900;
        text-transform: uppercase;
        font-style: italic;
        line-height: .8em;
        position: relative;
        bottom: 0px;
        transition: .5s;

        transform-style: preserve-3d;
        transform: translate3d(0, 0, 75px);
    }
    .pagefour-card-title-1 {
    }
    .pagefour-card-title-1::before {
        content: "";
        display: inline-block;
        width: 70px;
        height: 100%;
        /*background-color: crimson;*/
        margin-left: 15px;
    }
    .pagefour-card-title-2 {

    }
    .pagefour-card-title-2::before {
        content: "";
        display: inline-block;
        width: 100px;
        height: 100%;
        /*background-color: crimson;*/
        margin-left: 15px;
    }
    .pagefour-box-card:hover .pagefour-card-title-1, .pagefour-box-card:hover .pagefour-card-title-2 {
        transform: rotate(-20deg);
        bottom: -20px;
    }
     @supports (-webkit-text-stroke: 1px black) {
        .pagefour-card-title-1 {
            -webkit-text-stroke: 1px white;
            -webkit-text-fill-color: transparent;
        }
        .pagefour-card-title-2 {
            -webkit-text-stroke: 1px crimson;
            -webkit-text-fill-color: transparent;   
        }
        .pagefour-box-card:hover .pagefour-card-title-1 {
            -webkit-text-fill-color: white;   
        }
        .pagefour-box-card:hover .pagefour-card-title-2 {
            -webkit-text-fill-color: crimson;   
        }
        /* is active */
        .pagefour-card-title-1.pagefour-box-card-active {
            -webkit-text-fill-color: white;   
        }
        .pagefour-card-title-2.pagefour-box-card-active {
            -webkit-text-fill-color: crimson;   
        }
    }
    .pagefour-card-title-1.pagefour-box-card-active, .pagefour-card-title-2.pagefour-box-card-active {
        transform: rotate(-20deg);
        bottom: -20px;
    }
    .pagefour-box-card.pagefour-box-card-active {
        border: 4px solid crimson;
        cursor: pointer;
    }
    @media only screen and (max-width: 600px) {
        .pagefour-box-content {
        }
        .pagefour-img {
            transform: rotate(0deg);
            border: none;
        }
        .pagefour-box-img {
            /*width: 90%;*/
            transform: rotate(0deg);
        }
        .pagefour-box-img::before, .pagefour-box-img::after {
            font-size: 4em;
            transform: rotate(0deg);
        }
        .pagefour-box-img::before {
            top: 0;
            left: auto;
        }
        .pagefour-box-img::after {
            bottom: 0;
            right: auto;
        }
        .pagefour-box-card {
            padding-left: 10px
        }
        .pagefour-box-card::before, .pagefour-box-card::after {
            font-size: 3em;
        }
        .pagefour-box-card::before {
            top: 0;
            left: auto;
        }
        .pagefour-box-card::after {
            bottom: 0;
            right: auto;
        }
        .pagefour-card-title-1, .pagefour-card-title-2 {
            font-size: 4em;
            line-height: .9em;
        }
        .pagefour-card-title-1 {
        }
        .pagefour-card-title-1::before {
            width: 0;
            height: 0;
            margin-left: 0;
        }
        .pagefour-card-title-2 {

        }
        .pagefour-card-title-2::before {
            width: 0;
            height: 0;
            margin-left: 0;
        }
        .pagefour-box-card:hover .pagefour-card-title-1, .pagefour-box-card:hover .pagefour-card-title-2 {
            transform: rotate(-10deg);
            bottom: -20px;
        }
        .pagefour-card-title-1.pagefour-box-card-active, .pagefour-card-title-2.pagefour-box-card-active {
            transform: rotate(-10deg);
        }
    }
    @media only screen and  (min-width: 601px) and (max-width: 960px){
        .pagefour-img {
            transform: rotate(0deg);
            border: none;
        }
        .pagefour-box-img {
            /*width: 90%;*/
            transform: rotate(0deg);
        }
        .pagefour-box-img::before, .pagefour-box-img::after {
            font-size: 6em;
            transform: rotate(0deg);
        }
        .pagefour-box-img::before {
            top: 0;
            left: auto;
        }
        .pagefour-box-img::after {
            bottom: 0;
            right: auto;
        }
    }
</style>

<script>
    import ScrollOut from 'scroll-out'
    export default {
        name: 'PageFour',

        created() {
        },

        mounted: function() {
            // let elHtml = document.getElementsByTagName('html')[0]
            // elHtml.style.overflowY = 'hidden'
            // scroll
            // window.addEventListener('scroll', this.handleScroll);
            this.so = ScrollOut({
                scope: this.$el
            });
        },

        destroyed: function() {
            // let elHtml = document.getElementsByTagName('html')[0]
            // elHtml.style.overflowY = null
            // scroll
            // window.removeEventListener('scroll', this.handleScroll);
            this.so.teardown();
        },

        computed: {
            // 
        }, 

        data: () => ({
            fontColor: 'white',
            bgColor: '#0161b8',
            curentProduct: {
                id: 1,
                name: 'bag',
                src: 'nike-bag.png',
                descript: 'lorem ipsum',
            },
            dataProduct: [
                {
                    id: 1,
                    name: 'bag',
                    src: 'nike-bag.png',
                    descript: 'lorem ipsum',
                },
                {
                    id: 2,
                    name: 'jacket',
                    src: 'nike-jacket.png',
                    descript: 'lorem ipsum',
                },
                {
                    id: 3,
                    name: 't-shirt',
                    src: 'nike-t-shirt.png',
                    descript: 'lorem ipsum',
                }
            ],
        }),

        methods: {
            changeCurentProduct(i){
                this.curentProduct = this.dataProduct[i]
            }
        },
    }
</script>