<template>
	<div class="">
		<v-navigation-drawer
            class="hidden-md-and-up"
            v-model="drawer"
            app
            clipped
            disable-resize-watcher
            elevate-on-scroll
            color="">
            <v-list dense>
                <v-list-item link v-for="(item, i) in items" :key="i"
                    @click="$vuetify.goTo(item.link, options)">
                    <v-list-item-action>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>{{ item.title }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>

        <v-app-bar
            clipped-left
            elevation="0"
            :color="getCurentProduct.color"
            class="cola-color">
            <v-app-bar-nav-icon @click.stop="drawer = !drawer" class="hidden-md-and-up"></v-app-bar-nav-icon>
            <v-toolbar-title class="">
                <!-- <b style="text-transform: uppercase;">{{ currentRouteName }}</b> -->
                <div class="cola-nav-logo" style="background-color:;">
                    <v-img
                        :src="require('../../assets/cola/pepsi3.png')"
                        class="my-3 mt-15"
                        contain
                        width="250"/>
                </div>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items class="hidden-sm-and-down">
                <v-btn v-for="(item, i) in items" :key="i" :color="getCurentProduct.color" depressed
                    class="cola-color">
                    <span class="cola-nav-title">{{ item.title }}</span>
                </v-btn>
            </v-toolbar-items>
        </v-app-bar>
	</div>
    
</template>

<style scoped>
    .cola-nav-title {
        font-family: 'Montserrat', sans-serif;
        font-weight: 700;
    }
    .cola-color {
        transition: .5s;
    }
</style>

<script>
    import { mapGetters } from 'vuex'
    export default {
		name: 'NavigationBar',

		computed: {
            ...mapGetters({
                getCurentProduct: 'getCurentProduct',
            }),
			currentRouteName() {
				return this.$route.name
			},
            options () {
                return {
                    duration: this.duration,
                    offset: this.offset,
                    easing: this.easing,
                }
            },
		},

		created () {
			this.$vuetify.theme.dark = true
		},

		props: {
			source: String,
		},

		components: {
		// HelloWorld,
		},

        data: () => ({
			fab: false,
			transition: 'slide-x-reverse-transition',
			drawer: false,
			items: [
				{ title: 'home', icon: 'mdi-folder-open', link: '#cola-home', color:'black'},
                { title: 'products', icon: 'mdi-folder-open', link: '#cola-products', color:'black'},
                { title: "what's news", icon: 'mdi-folder-open', link: '#cola-news', color:'black'},
                { title: 'newsletter', icon: 'mdi-folder-open', link: '#cola-newsletter', color:'black'},
                { title: 'contact', icon: 'mdi-folder-open', link: '#cola-contact', color:'black'},
			],
			route_now: '',
			colorFont: '#0161b8',
            duration: 300,
            offset: 0,
            easing: 'easeInOutCubic',
        }),
    }
</script>