<template>
    <div class="pageLoader-box">
        <div class="loader"></div>
    </div>
</template>

<style scoped>
    *
    {
        margin:0;
        padding:0;
        box-sizing: border-box;
    }
    .pageLoader-box
    {
        display:flex;
        justify-content:center ;
        align-items:center;
        /*min-height:100vh;*/
        background:#000;
    }
    .loader 
    {
        position:relative;
        width: 150px;
        height: 150px;
        border-radius: 50%;
        background: linear-gradient(45deg,transparent, transparent 40%, crimson);
        animation: animate 2s linear infinite;
    }
    @keyframes animate
    {
        0%
        { 
            transform: rotate(0deg);
            /*filter:hue-rotate(0deg);*/
        }
        100%
        {
            transform: rotate(360deg);
            /*filter:hue-rotate(360deg);*/
        }
    }
    .loader:before
    {
        content: '';
        position: absolute;
        top: 6px;
        left: 6px;
        right: 6px;
        bottom: 6px;
        background: #000;
        border-radius:50%;
        z-index: 1000;
    }
    .loader:after
    {
        content: '';
        position:absolute;
        top:0px;
        left:0px;
        right:0px;
        bottom:0px;
        background:linear-gradient(45deg,transparent,transparent 40%, crimson);
        border-radius:50%;
        z-index:1;
        filter: blur(30px);
    }
</style>

<script>
    export default {
        name: 'PageLoaderGlow',

        mounted: function() {
            let elHtml = document.getElementsByTagName('html')[0]
            elHtml.style.overflowY = 'hidden'
        },

        destroyed: function() {
            let elHtml = document.getElementsByTagName('html')[0]
            elHtml.style.overflowY = null
        },

        data: () => ({
            // 
        }),

        methods: {
            // 
        },
    }
</script>