<template>
    <div class="">
        <v-app-bar
            fixed
            :color="colorFont"
            dark
            inverted-scroll
            height=""
            class="hidden-xs-only">
                <v-toolbar-title class="nav-title">nike</v-toolbar-title>
                <v-app-bar-nav-icon class="hidden-md-and-up" @click.stop="dialog = true"></v-app-bar-nav-icon>

                <v-spacer></v-spacer>

                <v-toolbar-items class="hidden-sm-and-down">
                    <v-btn v-for="(item, i) in items" :key="i" :color="colorFont" depressed
                        @click="$vuetify.goTo(item.link, options)"
                        class="nav-bar-btn">
                        <span class="nav-bar-menu">{{ item.title }}</span>
                    </v-btn>
                </v-toolbar-items>
        </v-app-bar>
        <v-dialog
            v-model="dialog"
            max-width="500">
            <v-card
                class="d-flex flex-column justify-center align-center">
                <div v-for="(item, i) in items" :key="i"
                    class="nav-bar-menu-dialog d-flex align-center"
                    @click="$vuetify.goTo(item.link, options);dialogFalse();">
                    {{ item.title }}
                </div>
            </v-card>
        </v-dialog>
        <div class="toolbar-box d-flex justify-center align-center d-sm-none">
            <div class="toolbar-box-small d-flex justify-center align-center px-1"
                style="overflow-y: scroll;">
                <div class="toolbar-logo mx-2"
                    style="font-size: 2em;font-family: 'Montserrat', sans-serif;font-weight: 700;font-style: italic;">
                    NIKE
                </div>
                <v-btn v-for="(item, i) in items" :key="i"
                    elevation="0"
                    class="mx-2"
                    style="padding:10px;border-radius: 10px;border: 1px solid crimson;box-sizing: border-box;"
                    @click="$vuetify.goTo(item.link, options)">
                    <v-icon
                        color="white"
                        style="font-size: 2em;">
                        {{ item.icon }}    
                    </v-icon>
                </v-btn>
            </div>
        </div>
    </div>
</template>

<style scoped>
    .nav-title, .nav-bar-menu {
        font-family: 'Montserrat', sans-serif;
        text-transform: uppercase;
    }
    .nav-title {
        font-weight: 900;
        font-size: 3em;
        font-style: italic;
        width: 100%;
        text-align: left;
    }
    .nav-bar-menu {
        font-weight: 700;
        font-weight: 1.5em;
        transition: .5s;
    }
    .nav-bar-btn:hover .nav-bar-menu {
        color: crimson;
    }
    .toolbar-box {
        position: fixed;
        bottom: 0;
        z-index: 20;
        width: 100%;
        height: 100px;
    }
    .toolbar-box-small {
        background-color: #272727;
        border-radius: 10px;
        /*border: 1px solid black;*/
        width: auto;
        height: 70%;
    }
    .nav-bar-menu-dialog {
        font-family: 'Montserrat', sans-serif;
        font-size: 2em;
        font-weight: 700;
        text-transform: uppercase;
        height: 100px;
        transition: .5s;
    }
    .nav-bar-menu-dialog:hover {
        cursor: pointer;
        color: crimson;
    }
    @media only screen and (max-width: 600px) {
    }
    @media only screen and (min-width: 601px) and (max-width: 960px){
    }
    @media only screen and (max-width: 460px){
        .toolbar-logo {
            display: none;
        }
    }
</style>

<script>
    export default {
        name: 'NavigationBar',

        computed: {
            currentRouteName() {
                return this.$route.name
            },
            options () {
                return {
                    duration: this.duration,
                    offset: this.offset,
                    easing: this.easing,
                }
            },
        },

        created () {
            // 
        },

        props: {
            source: String,
        },

        components: {
            // 
        },

        data: () => ({
            fab: false,
            transition: 'slide-x-reverse-transition',
            drawer: false,
            items: [
                { title: 'home', icon: 'mdi-home-outline', link: '#onepage-one', color:'black'},
                { title: 'product', icon: 'mdi-camera-iris', link: '#onepage-two', color:'black'},
                { title: 'feature', icon: 'mdi-star-outline', link: '#onepage-three', color:'black'},
                { title: 'ecosystem', icon: 'mdi-layers', link: '#onepage-four', color:'black'},
                // { title: 'five', icon: 'mdi-folder-open', link: '#onepage-five', color:'black'},
            ],
            route_now: '',
            colorFont: '#272727',
            duration: 1000,
            offset: 0,
            easing: 'easeInOutCubic',
            dialog: false,
        }),

        methods: {
            dialogFalse() {
                this.dialog = false
            }
        },
    }
</script>